#chatContainer {
  /* position: absolute; */
  z-index: 0;
  /* width: 100%; */
  /* height: 100%; */
}

#chatToolbar {
  height: 40px;
  background-color: #141414;
  box-sizing: border-box;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  padding-top: 4px;
  border-top-left-radius: 2vw;
  border-top-right-radius: 2vw;
  color: #f3f3f3;
}

#closeButton {
  position: absolute;
  right: 0;
  top: -8px;
}

#chatComponent {
  margin: 0px 0px 7.2px 2vw;
  height: 82.5vh;
  background-color: #beaee2;
  position: absolute;
  z-index: 99999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* margin: auto;
  height: calc(100% - 30px); */
  width: calc(100% - 30px);
  border-radius: 2vw;
}

.message-wrap {
  padding: 0 15px;
  height: calc(100% - 82px);
  overflow: auto;
}

.message {
  position: relative;
  padding: 7px 0;
}
.user-img {
  position: absolute;
  border-radius: 45px;
  width: 60px;
  height: 60px;
  top: 15px;
}

.msg-detail {
  width: calc(100% - 65px);
  display: inline-block;
}

.msg-detail p {
  margin: 0;
  font-size: 20px;
  padding: 2px 10px;
}

.msg-info p {
  font-size: 1em;
  color: #141414;
  font-style: italic;
}

.msg-content {
  position: relative;
  margin-top: 5px;
  border-radius: 5px;
  padding: 8px;
  color: #000000;
  width: auto;
  max-width: 80%;
}

span.triangle {
  border-radius: 2px;
  height: 8px;
  width: 8px;
  top: 12px;
  display: block;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
}

.text {
  word-break: break-all;
  color: #000000;
}

/* Start message from other user */

.message > .left .msg-detail .msg-info {
  text-align: left;
  padding-left: 1vw;
}

.message > .left .msg-detail .msg-info p {
  color: #f3f3f3;
}

.message.left .msg-detail {
  padding-left: 0.9vw;
}

.message.left .user-img {
  left: -5px;
  border: 1px solid #f0f0f094;
}

.message.left .msg-detail .msg-content {
  background-color: #141414;
  float: left;
}
.message.left .msg-detail .msg-content p {
  color: #f3f3f3;
}

.message.left .msg-detail .msg-content span.triangle {
  display: none;

  background-color: #f0f0f0;
  border-bottom-width: 0;
  border-left-width: 0;
  left: -5px;
}

/* End message from other user */

/* Start my messages */
.message.right .msg-detail {
  margin-left: 4vw !important;
}

.message.right .msg-detail .msg-info {
  padding-right: 1vw;
  text-align: right;
}
.message.right .user-img {
  right: -5px;
  border: 1px solid #c8ffe8ab;
}

.message.right .msg-detail .msg-content {
  background-color: #f3f3f3;

  float: right;
}
.message.right .msg-detail .msg-content span.triangle {
  display: none;
  background-color: #c8ffe8;
  border-bottom-width: 0;
  border-left-width: 0;
  right: -5px;
}

/* End my messages */

#messageInput {
  position: absolute;
  bottom: -2vh;
  width: 100%;
  background-color: #ffffff;
  text-align: center;
  padding: 10px 0px;
  height: 30px;
  border-bottom-left-radius: 2vw;
  border-bottom-right-radius: 2vw;
}
#messageInput input {
  width: 90%;
  height: 90%;
  border: none;
  outline: none;
  font-size: 20px;
  /* margin-left: -6%; */
  color: #000000;
}

#sendButton {
  background-color: #beaee2;
  position: absolute;
  right: 8px;
  top: 0;
  bottom: 3px;
  margin: auto;
  border: 1px solid #beaee2;
  box-shadow: none !important;
}
#sendButton {
  margin-left: 3px !important;
  margin-bottom: 2px !important;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #6b6b6b;
  visibility: hidden;
}

.chatComponentLight ::-webkit-scrollbar-thumb {
  background-color: #eeeeee !important;
  visibility: hidden;
}
