.OT_audio-level-meter {
  position: absolute;
  width: 25%;
  max-width: 224px;
  min-width: 21px;
  top: 0;
  right: 0;
  overflow: hidden;
}

.OT_audio-level-meter:before {
  /* makes the height of the container equals its width */
  content: "";
  display: block;
  padding-top: 100%;
}

.OT_audio-level-meter__bar {
  position: absolute;
  width: 192%; /* meter value can overflow of 8% */
  height: 192%;
  top: -96% /* half of the size */;
  right: -96%;
  border-radius: 50%;

  background-color: rgba(0, 0, 0, 0.8);
}

.OT_audio-level-meter__audio-only-img {
  position: absolute;
  top: 22%;
  right: 15%;
  width: 40%;

  opacity: 0.7;

  background: url(data:image/svg+xml;base64,)
    no-repeat center;
}

.OT_audio-level-meter__audio-only-img:before {
  /* makes the height of the container equals its width */
  content: "";
  display: block;
  padding-top: 100%;
}

.OT_audio-level-meter__value {
  position: absolute;
  border-radius: 50%;
  background-image: radial-gradient(
    circle,
    rgba(151, 206, 0, 1) 0%,
    rgba(151, 206, 0, 0) 100%
  );
}

.OT_audio-level-meter.OT_mode-off {
  display: none;
}

